<template>
    <v-container fluid fill-height style="padding:0;" id="transform" grid-list-md>
        <Blog :items="items" :time="time" :title="title">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="Explain">解释</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <p class=".body-1">1. <code>translate(x, y)</code> 移动画布</p>
                                    <p class=".body-1">2. <code>rotate(rad)</code> 旋转画布</p>
                                    <p class=".body-1">3. <code>scale(x, y)</code> 缩放画布</p>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
                <v-layout row wrap>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Result1">效果</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <canvas id="canvas" width="800px" height="500px"></canvas>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                    <v-flex sm12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="300"
                            >
                                <v-card-title>
                                    <h3 id="Code1">代码</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                    <v-tab key="html" ripple>HTML</v-tab>
                                    <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                    <v-tab-item key="html">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">
                                                    {{html}}
                                                </pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                    <v-tab-item key="js">
                                        <v-card flat>
                                            <v-card-text>
                                                <pre class="code">{{js}}</pre>
                                            </v-card-text>
                                        </v-card>
                                    </v-tab-item>
                                </v-tabs>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
    components: { Blog },
    data() {
        return {
            time: "2018-9-13",
            title: 'canvas变换的使用',
            items: [
                {
                    title: "解释",
                    target: "#Explain"
                },
                { divider: true },
                {
                    title: "效果",
                    target: "#Result1"
                },
                { divider: true },
                {
                    title: "代码",
                    target: "#Code1"
                }
            ],
            html: `
            <canvas 
                id="canvas" 
                width="800px" 
                height="500px"
            >
            </canvas>
            `,
            js: `
            const canvas = document.getElementById('canvas');
            const ctx = canvas.getContext('2d');
            ctx.strokeStyle = 'darkseagreen';
            ctx.fillStyle = 'green';
            
            let stepx = 0;
            let stepy = 0;
            let stepAng = 0;
            let stepSca = 0;
            let tmpx = 1;
            let tmpy = 1;
            let tmpSca = 0.1;
            let x = 0;
            let y = 0;
            let sx = 0;
            let sy = 0;
            window.requestAnimationFrame(draw);
            function draw() {
                // 移动
                x = 100+stepx;
                y = 100+stepy;
                if(x>700) { // 如果超过右边界，tmpx为-1
                    tmpx = -1;
                }else if( x < 100) { // 如果超过左边界，tmpx为1
                    tmpx = 1;
                }
                if(y>400) { // 如果超过底部边界，tmpy为-1
                    tmpy = -1;
                }else if( y < 100) { // 如果超过顶部边界，tmpy为1
                    tmpy = 1;
                }
                stepx+=tmpx;
                stepy+=tmpy;

                // 旋转
                stepAng += 10;

                // 缩放
                sx = stepSca;
                sy = 2-stepSca;
                if(stepSca > 2) { // 预设的缩放范围为 0 ~ 2，超过2让tmpSca为-0.1
                    tmpSca = -0.1;
                }else if (stepSca < 0) {
                    tmpSca = 0.1;
                }
                stepSca += tmpSca;

                ctx.save();
                ctx.clearRect(0, 0, canvas.width, canvas.height);

                ctx.translate(x, y);
                ctx.rotate((Math.PI / 180) * stepAng);
                ctx.scale(sx, sy);
                ctx.beginPath();
                ctx.arc(0, 0, 30, 0, (Math.PI/180)*360, false);
                ctx.fill();

                ctx.restore();
                window.requestAnimationFrame(draw);
            }
            `
        };
    },
    mounted() {
        const canvas = document.getElementById('canvas');
        const ctx = canvas.getContext('2d');
        ctx.strokeStyle = 'darkseagreen';
        ctx.fillStyle = 'green';
        
        let stepx = 0;
        let stepy = 0;
        let stepAng = 0;
        let stepSca = 0;
        let tmpx = 1;
        let tmpy = 1;
        let tmpSca = 0.1;
        let x = 0;
        let y = 0;
        let sx = 0;
        let sy = 0;
        window.requestAnimationFrame(draw);
        function draw() {
            // 移动
            x = 100+stepx;
            y = 100+stepy;
            if(x>700) { // 如果超过右边界，tmpx为-1
                tmpx = -1;
            }else if( x < 100) { // 如果超过左边界，tmpx为1
                tmpx = 1;
            }
            if(y>400) { // 如果超过底部边界，tmpy为-1
                tmpy = -1;
            }else if( y < 100) { // 如果超过顶部边界，tmpy为1
                tmpy = 1;
            }
            stepx+=tmpx;
            stepy+=tmpy;

            // 旋转
            stepAng += 10;

            // 缩放
            sx = stepSca;
            sy = 2-stepSca;
            if(stepSca > 2) { // 预设的缩放范围为 0 ~ 2，超过2让tmpSca为-0.1
                tmpSca = -0.1;
            }else if (stepSca < 0) {
                tmpSca = 0.1;
            }
            stepSca += tmpSca;

            ctx.save();
            ctx.clearRect(0, 0, canvas.width, canvas.height);

            ctx.translate(x, y);
            ctx.rotate((Math.PI / 180) * stepAng);
            ctx.scale(sx, sy);
            ctx.beginPath();
            ctx.arc(0, 0, 30, 0, (Math.PI/180)*360, false);
            ctx.fill();

            ctx.restore();
            window.requestAnimationFrame(draw);
        }
    }
};
</script>

<style lang="scss">
#transform {
    .blogcontent {
        padding-top: 20px;
    }
    .code {
        font-size: 12px;
        background-color: #f9f9f5;
        white-space: pre-wrap;
        word-wrap: break-word;
    }
}
</style>

